@tailwind base;
@tailwind components;
@tailwind utilities;

/**
s:text
**/
.field {
    @apply relative;
}

.field label {
    @apply block text-sm font-normal text-gray-700 top-0 transform-none transition-none #{!important};
}

.field input {
    @apply py-2 px-3 text-gray-900 border h-auto mt-1 text-base focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-lg pr-14 #{!important};
}

.field input ~ .link {
    @apply text-gray-900 font-normal text-xs absolute top-1/4 text-gray-400 hover:text-violet-700 right-3 #{!important};
}

/**
Dialog Style
**/
.ui-dialog {
    @apply bg-white shadow-xl rounded-xl z-101;
}

.ui-widget-header {
    @apply relative;
}

.ui-dialog-titlebar {
    @apply px-6 pt-6;
}

.ui-dialog-buttonpane {
    @apply px-6 pb-6 pt-2;
}

.ui-dialog-content {
    @apply px-6 py-6;
}

.ui-widget-overlay {
    @apply fixed inset-0 bg-gray-700 opacity-70;
}

.ui-dialog-title {
    @apply text-gray-900 font-medium text-lg;
}

.ui-dialog-content p {
    @apply text-gray-500 text-sm;
}

.ui-dialog-titlebar-close {

    @apply absolute right-0 top-0 p-3;
}

/**
Date Picker Style
**/
.ui-datepicker {
    @apply text-gray-700 mt-2 text-sm border bg-white shadow-xl border-b-gray-100 rounded rounded-md #{!important};
}

.ui-datepicker-group {
    @apply border border-b-gray-100;
}

.ui-datepicker-group:last-child {
    border: none;
}

.ui-datepicker-header {
    width: theme('width.full');
    @apply flex font-medium justify-between items-center px-6 pt-6 h-16;
}

.ui-datepicker-calendar-container {
    @apply px-6 py-6;
}

.ui-datepicker-prev {
    order: theme('order.1');
}

.ui-datepicker-next {
    order: theme('order.3');
}

.ui-datepicker-prev,
.ui-datepicker-next {
    content: ' ';
    @apply w-10 h-10 flex items-center justify-center;
}

.ui-datepicker-multi .ui-datepicker-title {
    margin: 0 auto;
}

.ui-datepicker-multi .ui-datepicker-buttonpane {
    @apply justify-end;
}

.ui-datepicker-multi .ui-datepicker-buttonpane > button {
    width: auto;
    flex: none;
}

.ui-datepicker-title {
    order: theme('order.2');
    @apply text-base rounded #{!important};
}

.ui-datepicker-calendar {
    width: theme('width.full');
    @apply px-6 py-6;
}

.ui-datepicker-calendar th {
    @apply text-sm font-medium py-2;
}

.ui-datepicker-calendar td {
    @apply text-sm font-normal text-center border-0;
}

.ui-state-default {
    @apply w-10 h-10 inline-flex items-center justify-center snap-align-none;
}

.ui-state-active {
    @apply bg-violet-600 text-white rounded-full;
}

.ui-datepicker-today {
    @apply relative;
}

.ui-datepicker-today > .ui-state-default {
    @apply relative bg-gray-100 rounded-full font-medium #{!important};
}

.ui-datepicker-today:after {
    content: ' ';
    @apply absolute w-1 h-1 inline-block rounded-full bg-violet-700 inset-x-1  bottom-1 m-auto;
}

.ui-datepicker-calendar .ui-state-hover {
    @apply relative bg-violet-100 text-violet-700 rounded-full font-medium #{!important};
}

.ui-datepicker-buttonpane {
    @apply flex justify-between items-center px-6 py-6 border-t border-t-gray-200;
}

.ui-datepicker-buttonpane > button {
    @apply flex-1;
}

.ui-datepicker-current {
    @apply mr-2 h-10 inline-flex shadow-xs border border-gray-300 items-center justify-center px-5 py-3 text-sm font-medium rounded-lg text-gray-700 hover:bg-gray-50 bg-white focus:outline-none focus:ring-4 focus:ring-gray-100;
}

.ui-datepicker-close {
    @apply ml-2 h-10 inline-flex shadow-xs items-center justify-center px-5 py-3 text-sm font-medium rounded-lg border border-violet-700 text-white hover:bg-violet-600 bg-violet-700 focus:outline-none focus:ring-4 focus:ring-violet-100;
}

.ui-datepicker-multi {
    @apply w-auto #{!important};
}
