.ch32-snackbar-wrap {
    display: block;
    position: fixed;
    width: 235px;
    pointer-events: none !important;
    margin: 0;
    padding: 0;
    letter-spacing: normal;
    z-index: 9000 !important;
}

.ch32-snackbar-wrap * {
    margin: 0;
    padding: 0;
}

.ch32-snackbar-wrap.bottom-left {
    bottom: 20px;
    left: 20px;
}

.ch32-snackbar-wrap.bottom-right {
    bottom: 20px;
    right: 40px;
}

.ch32-snackbar-wrap.top-left {
    top: 20px;
    left: 20px;
}

.ch32-snackbar-wrap.top-right {
    top: 20px;
    right: 40px;
}

.ch32-snackbar-single {
    @apply  block bg-white border border-gray-100 text-gray-900 w-full shadow-lg p-4 rounded-lg;

    width: 100%;
    margin: 0 0 5px;
    position: relative;
    pointer-events: all !important;

    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
}

.ch32-snackbar-single h2 {

    @apply text-sm mb-2 font-medium;

    background: none;
    color: inherit;
    line-height: inherit;
    letter-spacing: normal;
}

.ch32-snackbar-single a {
    color: #eee;
    text-decoration: none;
    font-weight: bold;
    border-bottom: 1px solid white;
    padding-bottom: 3px;
    font-size: 12px;
}

.ch32-snackbar-single ul {
    margin: 0 0 0 15px;
    background: none;
    padding: 0;
}

.ch32-snackbar-single ul li {
    list-style-type: disc !important;
    line-height: 17px;
    background: none;
    margin: 0;
    padding: 0;
    letter-spacing: normal;
}

.close-ch32-snackbar-single {
    position: absolute;
    top: 3px;
    right: 7px;
    font-size: 14px;
    cursor: pointer;
}

.ch32-snackbar-loader {
    display: block;
    position: absolute;
    top: -2px;
    height: 5px;
    width: 0;
    left: 0;
    border-radius: 5px;
    background: red;
}

.ch32-snackbar-loaded {
    width: 100%;
}

.ch32-has-icon {
    @apply  pl-9 pr-2 py-3;
    background-repeat: no-repeat;
    background-position: 10px;
}

.ch32-icon-info {
    background-image: url('data:image/png;base64,');
}

.ch32-icon-warning {
    background-image: url('data:image/png;base64,');
}

.ch32-icon-error {
    background-image: url('data:image/png;base64,');
}

.ch32-icon-success {
    background-image: url('data:image/svg+xml;base64,');
    background-position-x: 15px;
    background-position-y: 14px;
}
